<template>
  <div class="applicant">
    <j-header title="申请经纪人"></j-header>
    <main>
      <ul>
        <li>
          <p>姓 名</p>
          <jun-input v-model="form.relname"
            placeholder="请输入姓名"
            type='text'
            clear>
          </jun-input>
        </li>
        <li>
          <p>手机号码</p>
          <jun-input v-model="form.phone"
            placeholder="请输入手机号码"
            type='phone'
            disabled
            clear>
          </jun-input>
        </li>
        <li>
          <p>图形验证码</p>
          <jun-input v-model="imgcode"
            placeholder="请输入图形验证码"
            type='text'>
          </jun-input>
          <img-Code type='0'/>
        </li>
        <li>
          <p>短信验证码</p>
          <jun-input v-model="form.smscode"
            placeholder="请输入短信验证码"
            type='text'>
          </jun-input>
          <send-code :data='{ type: 0, phone: form.phone, code: imgcode }'/>
        </li>
      </ul>
      <button @click="modifi">确认提交</button>
    </main>
  </div>
</template>

<script>
// @ is an alias to /src
import JHeader from '@/components/JHeader'
import JunInput from '@/components/JunInput'
import imgCode from '@/components/imgCode'
import sendCode from '@/components/sendCode'
import { mapActions, mapGetters } from 'vuex'

export default {
  name: 'applicant',
  components: {
    JHeader,
    JunInput,
    imgCode,
    sendCode
  },
  data () {
    return {
      imgcode: '',
      form: {
        relname: '',
        phone: '',
        smscode: ''
      }
    }
  },
  computed: {
    ...mapGetters([
      'userInfo'
    ])
  },
  created () {
    this.form.phone = this.userInfo.phone
  },
  methods: {
    ...mapActions([
      'GetUserInfo'
    ]),
    async init (data) {
      const result = await this.request({
        url: '/agent',
        method: 'post',
        data: this.form
      })
      this.$toast({
        message: result.message,
        position: 'top'
      })
      result.code === 1 && this.$router.back()
    },
    modifi () {
      const { relname, smscode } = this.form
      if (!relname) {
        this.$toast({
          message: '请输入真实姓名',
          position: 'top'
        })
      } else if (!smscode) {
        this.$toast({
          message: '请输入短信验证码！',
          position: 'top'
        })
      } else {
        this.init()
      }
    }
  },
  mounted () {
  }
}
</script>

<style lang="scss" scoped>
  @import './index.scss';
</style>
